//
// SECTION FOOTER
// --------------------------------------------------------------

footer {
	background-color: $color-bg-about;
	font-family: $font-family;
	color: $color-text-dark;

	width: 100%;
	height: 120px;

	.section-content {
		padding: 30px 100px;

		.contact {
			float: left;
			display: block;
			width: auto;
			height: 40px;

			p {
				margin: 1em;
			}
		}

		button {
			position: absolute;
     		left: 50%;
     		transform: translateX(-50%);

     		width: 50px;
     		height: 50px;
     		border-radius: 50%;
     		font-size: 10px;
		}

		p {
			float: right;
		}
	}
}
