//
// SECTION MAIN
// --------------------------------------------------------------

.section-main {
	background-color: $color-main;
	color: $color-text-white;
	font-family: $font-family;
	position: relative;
	width: 100%;
	height: 650px;

	header {
		position: absolute;
		top: 2%;
		left: 4%;

		h2 {
			float: left;
			text-transform: uppercase;
			font-size: 1.2em;

			&:first-child {
				color: darken( $color-text-white, 25%)
			}
		}
	}

	.cloud-1 {
		width: 60px;
		height: auto;
		color: #fff;

		position: absolute;
		top: 35vh;
		left: 30vh;

		animation: float1 1.5s infinite;
	}

	.cloud-2 {
		width: 60px;
		height: auto;
		color: #fff;

		position: absolute;
		top: 20vh;
		right: 30vh;

		animation: float2 1.5s infinite;
	}

	.cloud-3 {
		width: 60px;
		height: auto;
		color: #fff;

		position: absolute;
		top: 20vh;
		right: 30vh;

		animation: float2 1.5s infinite;
	}

	.cloud-4 {
		width: 60px;
		height: auto;
		color: #fff;

		position: absolute;
		top: 20vh;
		right: 30vh;

		animation: float2 1.5s infinite;
	}

	.rocket {
		width: 55px;
		height: auto;
		position: absolute;
		bottom: 0;
		right: 20%;
	}

	.section-content{
		vertical-align: middle;
		padding: 12% 100px;
		text-align: center;

		}	
}

.typewriter {
	display: inline-block;

	h1 {
		overflow: hidden;
		border-right: 0.15em solid orange;
		white-space: nowrap;
		margin: 0 auto;
		letter-spacing: 0.10em;
		animation:
			typing 2s steps(30, end),
			blink-caret .5s step-end infinite;
	}
}

//
// SECTION ABOUT
// --------------------------------------------------------------

.section-one {
	background-color: $color-bg-about;
	color: $color-text-dark;
	font-family: $font-family;
	width: 100%;
	height: auto;

	.section-content {

		padding: 50px 100px;
		display: block;
		overflow: auto;
	
		h2 {
			text-align: center;
			margin-bottom: 1.5em;

		}

		img {
			width: 9.375em;
			float: left;
			border-radius: 50%;
			margin-left: 15%;

		}

		p {
			float: left;
			width: 600px;
			margin-top: 0;
			padding-left: 1em;
			margin-left: 10%;
			border-left: 1px solid;
		}

	}
}

//
// SECTION EDUCATION & EXPERTISE
// --------------------------------------------------------------

.section-two {
	background-color: $color-bg-education;
	font-family: $font-family;
	color: $color-text-white;
	width: 100%;
	height: auto;
	position: relative;

	.section-content {
		padding: 1.875em 6.250em;

		h2 {
			text-align: center;
			margin-bottom: 2.5em;
		}

		img {
			width: 3.750em;
		}
	}
}

.lines {
	display: inline-block;
	background-color: $color-text-white;

	.vertical-line1 {
		width: 1px;
		height: 348px;
	}

}

.horizontal-line1 {
	background-color: $color-text-white;
	width: 97.5px;
	height: 1px;
	position: absolute;
	top: 190px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
}


.horizontal-line2 {
	background-color: $color-text-white;
	width: 97.5px;
	height: 1px;
	position: absolute;
	top: 308px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
}

.horizontal-line3 {
	background-color: $color-text-white;
	width: 97.5px;
	height: 1px;
	position: absolute;
	top: 424px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
}

.content-left {
	width: 49.95%;
	float: left;
}

.content-right {
	width: 49.95%;
	float: right;
}

.education-1,
.education-2,
.education-3 {
	p {
		text-align: center;
		width: 20.875em;
		height: 5.000em;
		float: right;
		border: 1px solid;
		border-radius: 0.3em;
		padding: 1.3em 1.4em;
	}

	img {
		float: right;
		padding-top: 0.5em;
		margin: 1em 3em 2em 3em;
	}
}

.expertise-1,
.expertise-2 {
	img {
		float: left;
		padding-top: 0.5em;
		margin: 1em 3em 2em 3em;
	}

	div {
		float: left;
		width: 20.875em;
		height: 5.000em;
		text-align: center;
		border: 1px solid;
		border-radius: 0.3em;
		padding: 0.7em 1.4em;
		margin: 1em 0;

		h3 {
			margin: 0;
		}

		p {
			margin: 0.2em 0 0 0;
			font-size: 0.9em;
		}
	}
}

.expertise-3 {
	img {
		float: left;
		padding-top: 0.5em;
		margin: 1em 3em 2em 3em;
	}

	div {
		float: left;
		width: 20.875em;
		height: 5.000em;
		text-align: center;
		border: 1px solid;
		border-radius: 0.3em;
		padding: 0.7em 1.4em;
		margin: 1em 0;

		h3 {
			margin: 0.3em 0 0 0;
		}

		p {
			margin: 0.2em 0 0 0;
			font-size: 0.9em;
		}
	}
}

//
// SECTION PROJECTs
// --------------------------------------------------------------

.section-three {
	background-color: $color-main;
	font-family: $font-family;
	color: $color-text-white;
	width: 100%;
	height: auto;

	.section-content {
		padding: 30px 100px;
		text-align: center;

		h2 { 
			margin: 1.5em 0;

		}

		img {
			width: 220px;
			height: 150px;
			border: 1px solid;
		}
	}
}
